.header-div
{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--Light-Pink);
    padding: 12px 0;
    flex-wrap: wrap;
}
.header-div-text
{
    font-size: 13px;
    font-weight: 500;
    color: var(--White);
    margin-right: 255px;
}
.header-div-img
{
    width: 12px;
    margin-right: 8px;
}
.header-div_text
{
    font-size: 13px;
    font-weight: 500;
    color: var(--White);
    margin-right: 24px;
}
.header-div_img
{
    width: 18px;
}
/* ========================================================== */
.header-block
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    flex-wrap: wrap;
}
.header-list
{
    display: flex;
    gap: 0 50px;
}